import './App.css';
import React,{Component} from 'react';
class CounterComponent extends Component{
    constructor (props)
    {
        super(props);
        this.state={initialvalue:5}
        this.basestate=this.state.initialvalue;
    }
    //fixed=this.state.initialvalue;
   
    render(){
        return(
            <>
            <h3>{this.props.componentname} component </h3>
            <h4>intitial value:{this.state.initialvalue}</h4>
            <button className="btn" onClick={()=>this.setState({initialvalue:this.state.initialvalue+1})}>increment</button>
            <button className="btn" onClick={()=>this.setState({initialvalue:this.state.initialvalue-1})}>decrement</button><br></br>
           <button className="btn" onClick={()=>this.setState({initialvalue:this.basestate})}>reset</button>
            </>
        )
    }
}
export default CounterComponent;